/* Asegúrate de importar App.css en App.js si decides crearlo */
body, #root {
  height: 100vh;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
}

/* Estilos adicionales para App si tienes un App.css */
.App {
  text-align: center;
}
