.cookie-image {
    width: 150px; /* Controla el ancho de la imagen */
    height: auto; /* Mantiene la relación de aspecto de la imagen */
    cursor: pointer; /* Cambia el cursor a una mano cuando pasa sobre la imagen */
    display: block; /* Hace que la imagen sea un bloque para poder centrarla */
    margin: 0 auto; /* Centra la imagen horizontalmente */
    /* Añade un margen superior e inferior si lo necesitas */
    margin-top: 20px;
    margin-bottom: 20px;
  }
  
  /* Estilos adicionales para centrar verticalmente todo el contenido de la aplicación */
  /* Esto asume que quieres centrar el contenido en la pantalla */

  .floating-cookie {
    position: absolute;
    width: 50px; /* Tamaño más pequeño para las galletas flotantes */
    pointer-events: none; /* Asegura que no interfieran con los clics */
    animation: floatAway 1s ease-in forwards;
  }
  
  @keyframes floatAway {
    from {
      opacity: 1;
      transform: translate(-50%, -50%) scale(1);
    }
    to {
      opacity: 0;
      transform: translate(-50%, -50%) scale(0.5) translate(var(--translateX), var(--translateY));
    }
  
}